.no-outline() {
  outline: none;
  background: transparent;

  &::before {
    display: none;
  }
}

.list-reset() {
  margin: 0;
  padding: 0;
  list-style: none;
}

.visually-hidden() {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.white-background() {
  background-image: url("../img/bg-triangle-white.svg");
  background-repeat: no-repeat;
  background-position: 0 -65px;
}
